import React from "react";
import styles from "./MyProjects.module.scss"
import ProjectItem from "./projects/ProjectItem";
import socNetwork from './img/social_network.jpg'


let MyProjects = () => {

    let projects = [
        {id: 1, name: "Social network in progress...", img: socNetwork, url:'https://solnviktor.github.io/social_network/'},
        {id: 2, name: "Todo List in progress...", img: 'data:image/png;base64,', url:'#'},
        ]
    return (
        <div className={styles.myProjects} id="projects">
            <div className={styles.container}>
                <div className={styles.content}>
                    <div className={styles.title}>
                        <h2>My projects</h2>
                    </div>
                    <div className={styles.projects}>
                        { projects.map ( n => <ProjectItem key={n.id} name={n.name} img={n.img} url={n.url}/>)}
                    </div>
                </div>
            </div>
        </div>
    )
};

export default MyProjects;